.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;

    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;

    &:not(:last-child) {
      margin-right: 4px;
    }

    @for $stars-quantity from 1 through 5 {
      .stars--#{$stars-quantity} &:nth-child(-n + #{$stars-quantity}) {
        background-image: url(../images/star-active.svg);
      }
    }
  }
}
